<template>
<div class="zoom-file-upload">
  <div class="upload-header">
     <div class="upload-title"></div>
     <div class="alert-upload upload-info">
        <i class="zoom-icon close-alert icon-hint"></i>
        <span v-if="limit">{{limit}}!</span>
        <span v-if="size">{{size}}</span>
     </div>
     <div v-show="successCount" class="alert-upload upload-success">
        <i class="zoom-icon close-alert icon-success"></i>
        {{successCount}}
     </div>
     <div v-show="errCount" class="alert-upload upload-error">
        <i class="zoom-icon close-alert icon-close"></i>
        {{errCount}}!
     </div>
  </div>
  <div class="upload-toolbar zoom-clear">
      <zoom-button ref="addUpload" @click="addFileClick"></zoom-button>
      <div class="upload-switch">
          <zoom-radio v-model="active" :op="radioOp"></zoom-radio>
      </div>
  </div>
  <div class="upload-content" ref='select_frame'  ondragstart="return false">
    <div v-show="List.length === 0" class="upload-text">
        <i class="zoom-icon icon-edit"></i>
        <div></div>
    </div>
    <ul v-show="List.length > 0 && active === 'imgModel' " class="upload-file-list">
        <li v-for="(item, index) of List" :key="index" class="upload-file">
            <div class="upload-item">
                <img v-if="item.type && item.type.indexOf('image') > -1" :src="item.url" :alt="item.name">
                <span v-else class="zoom-icon icon-channel"></span>
            </div>
            <div class="file-name" title="">
                <span class="file-name-wrapper">{{item.name}}</span>
            </div>
            <div class="file-close">
                <a @click="removeConfirmation(index)" class="zoom-icon icon-delete"></a>
            </div>
            <div class="file-size">
                {{item.fileSize}}
            </div>
            <!--  -->
            <div v-show="item.status === 'success' || item.status === 'error' " class="file-response">
                <i :class="item.status === 'success' ? 'icon-success-fill' : 'icon-close-fill' " class="zoom-icon"></i>
            </div>
            <div v-if="!closeProgress" class="file-status">
                <zoom-progress :status="item.status" :progress="testprogress[index].progress"></zoom-progress>
            </div>
        </li>
    </ul>

    <div v-show="List.length > 0 && active === 'listModel' " class="zoom-grid">
        <div class="zoom-grid-body">
            <!--  -->
            <div class="grid-head">
                <div class="grid-headbox">
                    <table class="grid-table grid-thead">
                        <thead class="grid-head-content">
                            <tr>
                                <th v-show="closeProgress && item.id !== 5 || !closeProgress" v-for="item of title" :key="item.id" class="grid-item" :style="'width: ' + item.width + '%;' ">
                                    <span class="thead-title">{{item.text}}</span>
                                </th>
                            </tr>
                        </thead>
                    </table>
                </div>
            </div>
            <!--  -->
            <div class="grid-body">
                <div class="grid-bodybox">
                    <table class="grid-table grid-tbody">
                        <tbody class="grid-body-content">
                            <tr v-for="(item, index) of List" :key="index" class="grid-row">
                                <td class="grid-item" style="width: 30%;">
                                    <span class="grid-input">
                                        {{index + 1}}
                                    </span>
                                </td>
                                <td class="grid-item" style="width: 30%;">
                                    <span class="grid-input">
                                        <span>
                                            <a title="" class="zoom-icon">
                                                <span @click="removeConfirmation(index)" class="zoom-icon icon-delete"></span>
                                            </a>
                                        </span>
                                    </span>
                                </td>
                                <td :zoom-tip="item.fileName" class="grid-item zoom-tip" style="width: 100%;">
                                    <span class="grid-input">
                                        {{item.fileName}}
                                    </span>
                                </td>
                                <td class="grid-item" style="width: 50%;">
                                    <span class="grid-input">
                                        {{formatStatus(item.status)}}
                                    </span>
                                </td>
                                <td  v-if="!closeProgress" class="grid-item" style="width: 50%;">
                                    <span class="grid-input">
                                        <!--  -->
                                        <zoom-progress :status="item.status" :progress="testprogress[index].progress"></zoom-progress>
                                    </span>
                                </td>
                                <td class="grid-item" style="width: 40%;">
                                    <span class="grid-input">
                                        {{item.fileSize}}
                                    </span>
                                </td>
                                <td class="grid-item" style="width: 60%;">
                                    <span class="grid-input">
                                        {{item.fileDate}}
                                    </span>
                                </td>
                                <td :zoom-tip="item.type" class="grid-item zoom-tip" style="width: 120%;">
                                    <span class="grid-input">
                                        {{item.type}}
                                    </span>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    </div>

  </div>
  <div class="upload-footer">
      <div class="upload-btns">
          <zoom-button :disabled="true" ref="startUpload" type="primary" @click="submit"></zoom-button>
          <!-- <zoom-button></zoom-button>
          <zoom-button></zoom-button> -->
      </div>
  </div>
  <input style="display:none" @change="addFile" :multiple="multiple" type="file" :name="name" ref="zoom-upload"/>
</div>
</template>
<script>
export default {
 name: 'zoom-file-upload',
 props: {
    op: {
        type: Object,
        url: {  // 
            type: String,
            required: true
        },
        fileSize: String,   //    10M 10KB 10kb
        fileList: {    //  
            type: Array,
            default: function() {
            return [];
            }
        },
        closeProgress: Boolean, //  , false xhrSubmit, trufetchSubmit
        params: Object,  //   token    param: {param1: '', param2: '' },
        multiple: Boolean, //  
        limit: Number,     //  
        onChange: Function,    //
        onProgress: Function,  //  uploadProgress(index, progress)
        onSuccess: Function,   //indexindex  uploadSuccess(index, response)
        onFailed: Function,    //indexindex    uploadFailed(index, err)
        onBefore: Function,    // onBeforefalse
        onFinished: Function   //result: { success: , failed:  }   onFinished(result)
    },
    name: String,
 },
 data() {
     return {
         limit: 0,  //  
         data: {},  //  
         action: '',    //  
         closeProgress: false,  //  true
         multiple: false,   //  
         title: [
             {id: 1, text: '', width: 30},
             {id: 2, text: '', width: 30},
             {id: 3, text: '', width: 100},
             {id: 4, text: '', width: 50},
             {id: 5, text: '', width: 50},
             {id: 6, text: '', width: 40},
             {id: 7, text: '', width: 60},
             {id: 8, text: '', width: 120}
         ],
         testList: [],
         radioOp: {
             name: 'list',
             isdisabled: false,
             data: [
                 {text: '', value: 'listModel', checked: true},
                 {text: '', value: 'imgModel'}
             ]
         },
         testprogress: [],
         active: '',    // 
         successCount: 0,   //
         errCount: 0,       //
         size: '10GB',
         filelist: [],
         List: [],
     }
 },
 watch: {
     List(newVal, oldVal) {
         this.successCount = 0;
         this.errCount = 0;
         if (this.op && this.op.onChange) {
             this.op.onChange(newVal);
         }
        //  ,  
        if (newVal.length > 0) {
            this.$refs['startUpload'].isdisabled = false;
        } else {
            this.$refs['startUpload'].isdisabled = true;
        }
        // limit,   0
        if (newVal.length >= this.limit) {
            this.List.length = this.limit;
            this.$refs['addUpload'].isdisabled = true;
        } else {
            this.$refs['addUpload'].isdisabled = false;
        }
     }
 },
 created() {
     if (this.op) {
        if (this.op.url) {
            this.action = this.op.url;
        }
        if (this.op.fileSize) {
            this.size = this.op.fileSize;
        }
        if (this.op.params) {
            this.data = this.op.params;
        }
        if (this.op.multiple) {
            this.multiple = this.op.multiple;
        }
        if (this.op.limit) {
            this.limit = this.op.limit;
        }
        if (this.op.closeProgress) {
            this.closeProgress = this.op.closeProgress;
        }
     }
 },
 mounted() {
    this.successCount = 0;
    this.errCount = 0;
    let select_frame = this.$refs['select_frame'];
    select_frame.ondragleave = e => {
        e.preventDefault();  // 
    }
    select_frame.ondrop = e => {
        e.preventDefault();    // 
        const data = e.dataTransfer.files[0];  // 
        if (data.length < 1) {
            return;  // 
        }
        this.addFile({target: {files: [data]}})//
    }
    select_frame.ondragenter = e => {
        e.preventDefault();  // 
        select_frame.border = '2px dashed red'
    }
    select_frame.ondragover = e => {
        e.preventDefault();    // 
    }
 },
 methods: {
    //  
    formatStatus(val) {
        switch (val) {
            case 'success':
                return '';
            case 'error':
                return '';
            case 'start':
                return '';
            default:
                return '';
        }
    },
    //  
    addFileClick() {
        this.$refs['zoom-upload'].click()
    },
    //  
    testSize(file) {
        if (!this.size) {
            return true
        }
        let self = this;
        function test(unit) {
            if (self.size.indexOf(unit) > -1) {
                return Number(self.size.split(unit)[0]);
            } else {
                return false;
            }
        }
        let size = 0;
        if (test('G')) {
            size = test('G') * 1024 * 1024;
        } else if (test('g')) {
            size = test('g') * 1024 * 1024;
        } else if (test('M')) {
            size = test('M') * 1024;
        } else if (test('m')) {
            size = test('m') * 1024;
        } else if (test('k')) {
            size = test('k');
        } else if (test('K')) {
            size = test('K');
        } else {
            size = Number(this.size);
        }
        if(isNaN(size)) {
            throw Error('zoom-ui TypeError: size, , KB, MB, GB');
        } else {
            if (file && size) {
                let fileSize = file.size;
                // if (file.type.indexOf('image') == -1) {
                //     this.defeated('!');
                //     return false
                // }
                if(fileSize > size * 1024) {
                    this.$zoom.alert({
                        title: '',
                        content: `${this.size}!`,
                        type: 'warning'
                    })
                    return false;
                } else if (fileSize <= 0) {
                    this.$zoom.alert({
                        title: '',
                        content: '0! ',
                        type: 'warning'
                    })
                    return false;
                } else {
                    return true;
                }
            } else {
                return true
            }
            this.size = size;
        }
    },
    //  methods4
    //  
    addFile({target: {files}}){//inputonchange
        for(let i = 0, l = files.length; i < l; i++){
            if (this.testSize(files[i])) {
                // 
                let fileSize = 0;
                let size = files[i].size;
                if (Math.floor(size / 1024) < 1024) {
                    fileSize = Math.floor(size / 1024) + 'KB';
                } else if (Math.floor(size / 1024 / 1024) < 1024) {
                    fileSize = Math.floor(size / 1024 / 1024) + 'MB';
                } else if (Math.floor(size / 1024 / 1024 / 1024) < 1024) {
                    fileSize = Math.floor(size / 1024 / 1024 / 1024) + 'GB';
                } else {
                    fileSize = size + 'B';
                }
                files[i].fileName = files[i].name;  // 
                files[i].url = URL.createObjectURL(files[i]);//blob
                files[i].status = 'ready';//......
                files[i].id = (Math.random()*10000000).toString(16).substr(0,4)+'-'+(new Date()).getTime()+'-'+Math.random().toString().substr(2,5);    // id
                files[i].fileSize = fileSize;   //  
                files[i].fileDate = this.$zoom.dateFormat("YYYY-mm-dd HH:MM", files[i].lastModifiedDate); //  
                let obj = {     //   progress ,
                    progress: 0
                }
                this.testprogress.push(obj);
            } else {
                return;
            }
        }
        // let fileList = [...this.filelist];
        let fileList = [];
        if(this.multiple && files.length > 1){//
            fileList = [...fileList, ...files];
            let len = fileList.length;
            let limit = this.limit;
            if(limit && typeof limit === "number" && Math.ceil(limit) > 0 && len > limit){//limit
                limit = Math.ceil(limit);
                //  limit = limit > 10 ? 10 : limit;
                fileList = fileList.slice(len - limit);
            }
        } else {//fileList = files;files
            fileList = files;
        }
        let List = [];
        this.filelist.push(fileList);
        // delete
        this.filelist.forEach(item => {
            if (item.length > 1) {
                item.forEach(i => {
                    if (i.status !== 'delete') {
                        List.push(i)
                    }
                })
            } else {
                if (item[0].status !== 'delete') {
                    List.push(item[0])
                }
            }
        })
        this.List = Array.from(new Set(List));
        // this.onChange(this.List);//datafileList
    },
    //  
    removeConfirmation(index) {
        this.$zoom.popup({
            content: '?',
            status: 'primary',
            onClick: () => {
                this.remove(index);
            }
        })
    },
    //  ,index
    remove(index) {
        // let fileList = [...this.fileList];
        let fileList = [...this.List];
        if (fileList[index]) {
            let fileId = fileList[index].id;
            let files = this.$refs['zoom-upload'].files;
            let len = files.length;
            this.testprogress.splice(index, 1);
            // delete 
            if(fileList.length){
                for (var i = 0; i<len; i ++) {
                    if (files[i].fileId === fileId) {
                        // 
                        this.$refs['zoom-upload'].files[i].status = 'delete';
                        break;
                    }
                }
                fileList.splice(index, 1);
                // delete
                this.filelist.forEach(item => {
                    if (item.length === 1) {
                        item.status = 'delete';
                    } else {
                        item.forEach(i => {
                            if(fileId === i.id) {
                                i.status = 'delete';
                            }
                        })
                    }
                })
                this.List = fileList;
            }
        }
    },
    //  fetchfetchXMLHttpRequestfetch
    submit() {
        this.successCount = 0;
        this.errCount = 0;
        if(this.checkIfCanUpload()){
            if(!this.closeProgress && typeof XMLHttpRequest !== 'undefined')
                this.xhrSubmit();
            else
                this.fetchSubmit();
        } else {
            this.$zoom.alert({
                title: '',
                content: '',
                type: 'warning'
            })
        }
        setTimeout(() => {
            this.$refs['startUpload'].isdisabled = true;
        });
    },
    // 4.xhrSubmitfetchSubmit
    fetchSubmit() {
        this.successCount = 0;
        this.errCount = 0;
        let keys = Object.keys(this.data), values = Object.values(this.data), action = this.action;
        const promises = this.List.map(each => {
            each.status = "start";
            let data = new FormData();
            data.append(this.name || 'file', each);
            keys.forEach((one, index) => data.append(one, values[index]));
            return fetch(action, {
                method: 'POST',
                headers: {
                    "Content-Type" : "application/x-www-form-urlencoded"
                },
                body: data
            }).then(res => {    //res.text()
                res.text();
                if (res.status === 200) {
                    each.status = 'success';
                } else {
                    each.status = 'error';
                }
            }).catch(err => {
                console.error('zoom-ui Error: ', err);
            })
        });
        Promise.all(promises).then(resArray => {//
            let success = 0, failed = 0;
            resArray.forEach((res, index) => {
                if (res.code === 1) {
                    success++;         //
                    this.List[index].status = 'success';
                    if (this.op && this.op.onSuccess) {
                        this.op.onSuccess(index, res);
                    }
                } else {
                    failed++;         //
                    this.List[index].status = 'error';
                    if (this.op && this.op.onFailed) {
                        this.op.onFailed(index, res);
                    }
                }
                this.successCount = success;
                this.errCount = failed;
            });
            return { success, failed };   //
        }).then( res => {
            if (this.op && this.op.onFinished) {
                this.op.onFinished(res);
            }
        });      //
    },
    xhrSubmit(){
        const _this = this;
        let options = this.List.map((rawFile, index) => ({
            file: rawFile,
            data: _this.data,
            filename: _this.name || "file",
            action: _this.action,
            onProgress(e){
                _this.$set(_this.testprogress[index], 'progress', e.percent);
                if (_this.op && _this.op.onProgress) {
                    _this.op.onProgress(index, e); //index
                }
            },
            onSuccess(res){
                _this.successCount += 1;
                rawFile.status = 'success';
                if (_this.op && _this.op.onSuccess) {
                    _this.op.onSuccess(index, res);
                }
                // 
                // _this.remove(index);
            },
            onError(err){
                _this.errCount += 1;
                rawFile.status = 'error';
                if (_this.op && _this.op.onFailed) {
                    _this.op.onFailed(index, err);
                }
            }
        }));
        let len = this.List.length;
        let send = options => {
            for(let i = 0; i < len; i++){
                if (options[i].file.status === 'ready') {
                    options[i].file.status = 'start'
                    _this.sendRequest(options[i]);//this.sendRequestthis.sendRequest
                }
            }
        };
        send(options);
        this.List = []
        options.forEach(item => {
            this.List.push(item.file);
        })
    },
    // element-ui
    sendRequest(option){
        const _this = this;
        upload(option);

        function getError(action, option, xhr) {
            var msg = void 0;
        if (xhr.response) {
            msg = xhr.status + ' ' + (xhr.response.error || xhr.response);
        } else if (xhr.responseText) {
            msg = xhr.status + ' ' + xhr.responseText;
        } else {
            msg = 'fail to post ' + action + ' ' + xhr.status;
        }
            var err = new Error(msg);
            err.status = xhr.status;
            err.method = 'post';
            err.url = action;
            return err;
        }

        function getBody(xhr) {
            var text = xhr.responseText || xhr.response;
            if (!text) {
                return text;
            }

            try {
                return JSON.parse(text);
            } catch (e) {
                return text;
            }
        }

        function upload(option) {
            if (typeof XMLHttpRequest === 'undefined') {
            return;
        }

        var xhr = new XMLHttpRequest();
        var action = option.action;

        if (xhr.upload) {
            xhr.upload.onprogress = function progress(e) {
                if (e.total > 0) {
                    e.percent = e.loaded / e.total * 100;
                }
                option.onProgress(e);
            };
        }
        var formData = new FormData();

        if (option.data) {
            Object.keys(option.data).map(function (key) {
                formData.append(key, option.data[key]);
            });
        }

        formData.append(option.filename, option.file);

        xhr.onerror = function error(e) {
            option.onError(e);
        };

        xhr.onload = function onload() {
        if (xhr.status < 200 || xhr.status >= 300) {
            return option.onError(getError(action, option, xhr));
        }

        option.onSuccess(getBody(xhr));
        };

        xhr.open('post', action, true);

        if (option.withCredentials && 'withCredentials' in xhr) {
            xhr.withCredentials = true;
        }

        var headers = option.headers || {};

        for (var item in headers) {
            if (headers.hasOwnProperty(item) && headers[item] !== null) {
                xhr.setRequestHeader(item, headers[item]);
            }
        }
            xhr.send(formData);
            return xhr;
        }
    },
    // onBeforefalse
    // on-progressXMLHttpRequestfetch
    checkIfCanUpload(){
        return this.List.length ? (this.op && this.op.onBefore && this.op.onBefore(this.List) || !this.op.onBefore) : false;
    }
 }
}
</script>
<style>
.zoom-file-upload .upload-content th.grid-item:first-child {
    text-align: center;
}
.zoom-file-upload .upload-content .grid-input .zoom-progress {
    line-height: 0;
}
.zoom-file-upload .upload-content .file-response .zoom-icon {
    font-size: 50px;
    position: relative;
    top: 10px;
    z-index: 3;
}
.zoom-file-upload .upload-content .file-response .icon-close-fill {
    color: #f5222d;
}
.zoom-file-upload .upload-content .file-response .icon-success-fill {
    color: #52c41a;
}
.zoom-file-upload .upload-content .file-response::after {
    content: "";
    width: 100%;
    height: 75%;
    position: absolute;
    left: 0;
    top: 0;
    filter: blur(23px);
    background: #fff;
    opacity: .8;
    z-index: 2;
}
.zoom-file-upload .upload-content .file-response {
    position: absolute;
    text-align: center;
    width: 100%;
    height: 100%;
    z-index: 1;
    top: 0;
}
.zoom-file-upload .upload-content .file-status .zoom-progress-container {
    height: 2px;
}
.upload-content .upload-file .file-status .zoom-progress span {
    font-size: 3px;
}
.upload-content .upload-file .file-status {
    position: relative;
    bottom: 0;
    right: 0;
    /* height: 2px; */
    max-height: 30px;
}
.upload-content .upload-file .file-close {
    z-index: 11;
    cursor: pointer;
    position: absolute;
    bottom: 2px;
    right: 5px;
}
.upload-content .upload-file .file-name {
    font-weight: 700;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.upload-content .upload-file-list li.upload-file .upload-item .icon-channel {
    font-size: 50px;
}
.upload-content .upload-file-list li.upload-file .upload-item img {
    width: 60px;
    height: 60px;
}
.upload-content .upload-file-list li.upload-file .upload-item {
    width: 100%;
    height: 60px;
    line-height: 60px;
    text-align: center;
    overflow: hidden;
    font-size: .875em;
    font-weight: 700;
}
.upload-content .upload-file-list li.upload-file {
    width: 100px;
    padding: 6px;
    margin: 0 8px 8px 0;
    float: left;
    position: relative;
    font-size: 12px;
    background: rgba(51, 51, 51, .06);
}
.upload-content .upload-file-list {
    font-size: 12px;
    list-style: none;
    margin: 0;
    padding: 0;
}
.zoom-file-upload .upload-footer .upload-btns>.btn {
    margin-left: 5px;
}
.zoom-file-upload .upload-footer .upload-btns {
    width: 100%;
    position: relative;
    text-align: center;
}
.zoom-file-upload .upload-footer {
    width: 100%;
    margin-top: 10px;
}
.zoom-file-upload .upload-content .upload-text>.zoom-icon {
    margin-top: 50px;
    font-size: 3.6em;
    color: #bfbfbf;
}
.zoom-file-upload .upload-content .upload-text {
    cursor: pointer;
    text-align: center;
    padding: 20px 0;
}
.zoom-file-upload .upload-content {
    position: relative;
    min-width: 200px;
    overflow: auto;
    max-height: 260px;
    border: 1px solid #d9d9d9;
}
.zoom-file-upload .upload-toolbar .zoom-btn {
